@import url(https://fonts.googleapis.com/css2?family=Jost:wght@500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost:wght@500;600;700&display=swap);
body {
  font-family: 'Jost', sans-serif;
  font-weight: 600;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 2em auto;
  background-color: black;
  color: whitesmoke;
}

form {
  display: flex;
  flex-direction: row;
  width: 20em;
  margin: 1em;
}

input[type=text] {
  padding: 1em;
  border: 1px solid white;
  width: 80%;
  color: white;
  background-color: black;
  font-family: 'Jost', sans-serif;
  font-weight: 600;
  font-size: 1em;
}

button {
  padding: 1em;
  width: 20%;
  border: 1px solid white;
  color: white;
  background-color: #222;
  font-size: 1em;
}

button:hover {
  background-color: #444;
}
.container {
  display: flex;
  flex-direction: row;
  width: 18em;
  justify-content: space-between;
  margin: 1em;
  padding: 1em;
  border: 1px solid white;
}

.taskName::-webkit-scrollbar { 
  display: none;  /* Safari and Chrome */
}

.taskName {
  overflow: scroll;
}

.taskName p {
  margin: 0;
  padding: 0;
}

.taskCompleted {
  -webkit-text-decoration: line-through 2.5px;
          text-decoration: line-through 2.5px;
}

.buttons {
  font-size: 120%;
  display: flex;
  flex-direction: row;
  margin-left: 1em;
}

.buttons .space {
  width: 0.5em;
}

.disabledButton {
  color: #444;
}

.container:hover {
  background-color: #222;
}
/* * {
  border: 1px dotted white;
} */

.ToDoApp {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  font-size: 250%;
  margin-bottom: 0;
}

